import React from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import cityType from 'components/__types__/city';

const CityFieldTable = ({ t, city }) => (
  <Table>
    <TableHead>
      <TableRow>
        <TableCell>{t('common.name')}</TableCell>
        <TableCell>{t('common.value')}</TableCell>
      </TableRow>
    </TableHead>
    <TableBody>
      <TableRow>
        <TableCell>
          <span>{t('entities.city.id')}</span>
        </TableCell>
        <TableCell data-testid="cityIdValue">
          <span>{city.id}</span>
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell>
          <span>{t('entities.city.name')}</span>
        </TableCell>
        <TableCell>
          <span>{city.name}</span>
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell>
          <span>{t('entities.city.state')}</span>
        </TableCell>
        <TableCell>
          <span>{city.state}</span>
        </TableCell>
      </TableRow>
    </TableBody>
  </Table>
);

CityFieldTable.propTypes = {
  city: cityType,
  t: PropTypes.func.isRequired,
};

CityFieldTable.defaultProps = {
  city: [],
};

export default withTranslation()(CityFieldTable);
